<template>
  <div class="articleManagement">
    <el-dialog title="" :visible.sync="dialogFormVisible" @close="closeDialog" class="dialog">
      <el-form :model="form" ref="form" :rules="rules">
        <el-form-item label="" :label-width="formLabelWidth" prop="title">
          <el-input v-model="form.title" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="preface">
          <el-input v-model="form.preface" type="textarea" autocomplete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="cover_photo">
          <el-upload
            :action="uploadUrl"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :show-file-list="false">
            <el-button type="primary"></el-button>
          </el-upload>
          <el-input v-model="form.cover_photo" style="display: none;" disabled></el-input>
          <img :src="form.cover_photo" alt="">
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="type">
          <el-select v-model="form.type" placeholder="">
            <el-option :label="item.title" :value="item.title" v-for="item in typeArr" :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="content">
          <el-input v-model="form.content" style="display: none;" disabled></el-input>
          <tinymce-editor ref="tinymce" :data="form.content" @textChange="contentChange"></tinymce-editor>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submit('form')"> </el-button>
      </div>
    </el-dialog>
    <div class="top">
      <el-form :inline="true" :model="fromObj">
        <el-form-item label="">
          <el-input v-model="fromObj.title" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="fromObj.type" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option :label="item.title" :value="item.title" v-for="item in typeArr" :key="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="find"></el-button>
        </el-form-item>
      </el-form>
      <div class="right">
        <el-button type="primary" @click="add"></el-button>
      </div>
    </div>
    <div class="content">
      <el-table
        :lazy="true"
        :data="tableData"
        border
        style="width: 100%">
        <el-table-column
          type="selection"
          align="center"
          width="40">
        </el-table-column>
        <el-table-column
          prop="title"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="preface"
          align="center"
          label="">
        </el-table-column>
        <el-table-column
          prop="cover_photo"
          align="center"
          label="">
          <template slot-scope="scope">
            <img :src="scope.row.cover_photo" alt="">
          </template>
        </el-table-column>
        <el-table-column
          prop="type"
          align="center"
          label=""
          width="150">
        </el-table-column>
          <el-table-column
          prop="browse"
          align="center"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="love"
          align="center"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="createdAt"
          align="center"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          label=""
          align="center"
          width="100">
          <template slot-scope="scope">
            <el-button @click="edit(scope.row)" type="text" class="edit"></el-button>
            <el-button @click="del(scope.row)" type="text" class="del"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import TinymceEditor from '../../../components/tinymce-editor/index'
import { rules } from '@/utils/validate'
import { findArticle, addArticle, deleteArticle, updateArticle, findArticleType } from '../../../api/admin/article'
import moment from 'moment'
export default {
  components: { TinymceEditor },
  data () {
    return {
      uploadUrl: '/uploadImg',
      title: '',
      total: 0,
      currentPage: 1,
      pageSize: 10,
      fromObj: {
        title: '',
        type: ''
      },
      typeArr: [],
      tableData: [],
      dialogFormVisible: false,
      formLabelWidth: '120px',
      rules: {
        title: [
          { required: true, validator: rules.string, trigger: 'blur', message: '' }
        ],
        preface: [
          { required: true, validator: rules.string, trigger: 'blur', message: '' }
        ],
        cover_photo: [
          { required: true, validator: rules.string, trigger: 'change', message: '' }
        ],
        content: [
          { required: true, validator: rules.string, trigger: 'change', message: '' }
        ],
        type: [
          { required: true, validator: rules.string, trigger: 'change', message: '' }
        ]
      },
      form: {
        id: null,
        title: '',
        preface: '',
        cover_photo: '',
        content: '',
        type: ''
      },
      findQuery: {
        u_id: JSON.parse(sessionStorage.getItem('userInfo')).id,
        type: null,
        title: null,
        page: 1,
        limit: 10
      }
    }
  },
  methods: {
    contentChange (val) {
      this.form.content = val
    },
    // 
    uploadSuccess (response, file, fileList) {
      this.form.cover_photo = response.data.url
      console.log(response)
      this.$message({
        type: 'success',
        message: ''
      })
    },
    // 
    uploadError (err, file, fileList) {
      this.$message({
        type: 'error',
        message: ''
      })
      console.log(err)
    },
    find () {
      this.findQuery.type = this.fromObj.type
      this.findQuery.title = this.fromObj.title
      this.getDatas()
    },
    submit (formName) {
      let _this = this
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(_this.$refs.tinymce.content)
          let query = {
            title: _this.form.title,
            preface: _this.form.preface,
            content: _this.$refs.tinymce.content,
            type: _this.form.type,
            cover_photo: _this.form.cover_photo
          }
          if (_this.form.id) {
            query.id = _this.form.id
            updateArticle(query).then(res => {
              console.log(res)
              if (res.code === 0) {
                _this.$message({
                  type: 'success',
                  message: res.data
                })
              }
              _this.getDatas()
            })
          } else {
            query.u_id = JSON.parse(sessionStorage.getItem('userInfo')).id
            addArticle(query).then(res => {
              console.log(res)
              if (res.code === 0) {
                _this.$message({
                  type: 'success',
                  message: res.data
                })
              }
              _this.getDatas()
            })
          }
          _this.dialogFormVisible = false
        } else {
          return false
        }
      })
    },
    closeDialog () {
      this.form = {
        id: null,
        title: '',
        cover_photo: '',
        content: '',
        type: this.typeArr[0].title
      }
      this.$refs.form.clearValidate()
    },
    getTypes () {
      findArticleType({ u_id: JSON.parse(sessionStorage.getItem('userInfo')).id }).then(res => {
        if (res.code === 0) {
          this.typeArr = res.data
        }
        this.form.type = this.typeArr[0].title
        console.log(this.form)
      })
    },
    getDatas () {
      findArticle(this.findQuery).then(res => {
        res.data.forEach(item => {
          item.createdAt = moment(item.createdAt).format('YYYY-MM-DD')
        })
        this.tableData = res.data
        this.total = res.count
      })
    },
    add () {
      this.form.id = null
      this.title = ''
      this.dialogFormVisible = true
    },
    edit (row) {
      console.log(row)
      this.form.id = row.id
      this.title = ''
      this.form.type = row.type
      this.form.title = row.title
      this.form.preface = row.preface
      this.form.cover_photo = row.cover_photo
      this.form.content = row.content
      this.dialogFormVisible = true
    },
    del (row) {
      let _this = this
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteArticle({ id: row.id }).then(res => {
          if (res.code === 0) {
            _this.$message({
              type: 'success',
              message: res.data
            })
            _this.getDatas()
          }
        })
      }).catch(() => {
        _this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    handleSizeChange (val) {
      console.log(` ${val} `)
      this.pageSize = val
      this.findQuery.limit = val
      this.getDatas()
    },
    handleCurrentChange (val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.findQuery.page = val
      this.getDatas()
    }
  },
  mounted () {
    this.uploadUrl = this.baseUrl + this.uploadUrl
    this.getDatas()
    this.getTypes()
  }
}
</script>

<style scoped lang="less">
  .articleManagement{
    padding: 30px;
    box-sizing: border-box;
    .dialog{
      img{
        width: 200px;
        margin: 20px 0 0;
      }
    }
    .top{
      display: flex;
      width: 100%;
      justify-content: space-between;
    }
    .content{
      overflow: auto;
      margin-bottom: 50px;
      img{
        width: 100%;
      }
    }
    .page{
      display: flex;
      align-items: center;
      justify-content: center;
      background: #fff;
      height: 50px;
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 999;
    }
  }
</style>
